.card {
  padding: 1rem;
  font-size: 1.2rem;
}

.card form {
  display: flex;
  justify-content: space-evenly;
  align-items: baseline;
}

.card form .input-field {
  flex-basis: 80%;
  margin-right: 1rem;
}

.card form .btn {
  flex-basis: 11%;
  margin-left: 1rem;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.flex span {
  padding: 0;
  margin: 0;
  line-height: 2;
}

.completed span {
  text-decoration: line-through;
}

.todo-list .todo-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 1200px) {
  .card form {
    flex-direction: column;
  }

  .card form .input-field {
    width: 100%;
  }

  .card form .btn {
    margin-left: 0;
  }
}